export default function(todo) {
  this.setState(
    {
      todoList: {
        ...this.state.todoList,
        [todo.id || this.state.todoCount]: {
          id: todo.id || this.state.todoCount,
          label: todo.label,
          completed: todo.completed
        }
      },
      todoCount: this.state.todoCount + 1
    },
    () => {
      this.filterTodo({});
    }
  );
}
