import { copyObj } from "./helper";

export default function(id) {
  const list = copyObj(this.state.todoList);
  const currentList = copyObj(this.state.currentList);

  list[id].completed = !list[id].completed;
  currentList[id].completed = !currentList[id].completed;

  this.setState({
    todoList: list,
    currentList: currentList
  });
}
