import React from "react";

export default class Filter extends React.Component {
  constructor() {
    super();
    this.state = {
      active: "ALL"
    };
  }
  getCssClass(type) {
    if (type === this.state.active) return "btn btn-primary active";
    else return "btn btn-dark";
  }
  render() {
    return (
      <div className="btn-group" role="group">
        <button
          onClick={() => {
            this.setState({
              active: "ALL"
            });
            this.props.filterTodo({});
          }}
          type="button"
          className={this.getCssClass("ALL")}
        >
          All
        </button>
        <button
          type="button"
          onClick={() => {
            this.setState({
              active: "ACTIVE"
            });
            this.props.filterTodo({ completed: false });
          }}
          className={this.getCssClass("ACTIVE")}
        >
          Active
        </button>
        <button
          type="button"
          onClick={() => {
            this.setState({
              active: "DONE"
            });
            this.props.filterTodo({ completed: true });
          }}
          className={this.getCssClass("DONE")}
        >
          Done
        </button>
      </div>
    );
  }
}
