import { filterByOptions, copyObj } from "./helper";

export default function(filter) {
  console.table(this.state.todoList);
  const list = copyObj(this.state.todoList);
  filter = { ...filter };
  Object.keys(list).forEach(key => {
    if (!filterByOptions(list[key], filter)) {
      delete list[list[key].id];
    }
  });
  this.setState({
    currentList: list
  });
}
