import React from "react";

import Title from "./Title";
import List from "./List";
import Search from "./Search";
import Create from "./Create";
import Filter from "./Filter";

import { removeTodo, createTodo, filterTodo, compliteTodo } from "./buisness";
import "../assets/main.css";
import serverAPI from "../serverAPI";

export default class App extends React.Component {
  state = {
    todoList: {},
    currentList: {},
    todoCount: 0,
    appTitle: "React Todo"
  };

  removeTodo = removeTodo.bind(this);
  complite = compliteTodo.bind(this);
  createTodo = createTodo.bind(this);
  filterTodo = filterTodo.bind(this);

  componentDidMount() {
    for (let i = 1; i < 101; i++) {
      serverAPI.getItem(i).then(todo => {
        this.createTodo({
          id: todo.id,
          label: todo.title,
          completed: todo.completed
        });
      });
    }
    this.filterTodo({});
  }
  render() {
    return (
      <div className="container app">
        <div className="row">
          <div className="col-8 mx-auto">
            <Title title={this.state.appTitle} count={this.state.todoCount} />
            <div className="row box">
              <div className="col-7">
                <Search filterTodo={this.filterTodo} />
              </div>
              <div className="col-5 display-flex filter">
                <Filter filterTodo={this.filterTodo} />
              </div>
            </div>
            <List
              list={this.state.currentList}
              removeTodo={this.removeTodo}
              complite={this.complite}
            />
            <Create createTodo={this.createTodo} />
          </div>
        </div>
      </div>
    );
  }
}
