export function strFilter(str, subStr) {
  return str.toLowerCase().indexOf(subStr.toLowerCase()) !== -1 ? true : false;
}

export function filterByOptions(obj, options) {
  for (let option in options) {
    if (option !== "label") return options[option] === obj[option];
    else {
      return strFilter(obj.label, options.label);
    }
  }
  return true;
}

export function copyObj(obj) {
  return JSON.parse(JSON.stringify(obj));
}
