import React from "react";

export default class Item extends React.Component {
  remove = e => {
    this.props.removeTodo(this.props.options.id);
  };
  complite = e => {
    console.log("asdasdasdasdsad ");
    this.props.complite(this.props.options.id);
  };
  render() {
    return (
      <div
        className={
          !this.props.options.completed
            ? "item box box-sm"
            : "complite-todo item box box-sm"
        }
      >
        <span className="todo-label">{this.props.options.label}</span>
        <span className="btn-group">
          <button
            onClick={this.complite}
            className="btn  btn-icons btn-outline-success"
          >
            <i className="material-icons xs">done_outline</i>
          </button>
          <button
            onClick={this.remove}
            className="btn  btn-icons btn-outline-danger"
          >
            <i className="material-icons xs">delete_outline</i>
          </button>
        </span>
      </div>
    );
  }
}
